

#header{
    width: 100%;
    height: 80vh;
    margin-top: 38px;
}

#header h1{
    margin: 0 0 10px 0;
    font-size: 40px;
}

#header h2{
    font-size: 25px;
    font-weight: 400;
    color: rgb(41, 41, 41);
}

#header .brand-name{
    color:rgb(107, 241, 197)  ;
}

#header .btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: rgb(19, 18, 20);
    border:1.5px solid rgb(249, 252, 83);
    text-decoration: none;
}
#header .btn-get-started:hover{
    color: rgb(29, 29, 29);
    background-color: rgb(139, 139, 101);
}

strong{
    color: rgb(255, 201, 113) !important;
}

.header-img{
    text-align: right;
}

#header .animated{
    /* animation: up-down 2s ease-in-out infinite alternate-reverse both; */
    animation: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

